import { HttpHeaders } from "@angular/common/http";

import { LOCAL_CLIENT_APP_COMMENT_KEY } from "../globals";
import { HeadersBaseList } from "./models/headers-base-list";

export function getDefaultApolloHeadersBase(): { [name: string]: string | string[] } {
  return {
    // Authorization: `Bearer ${localStorage.getItem("token")}`,
    // "X-Hasura-Admin-Secret": `${localStorage.getItem("X-Hasura-Admin-Secret")}`,
    // Ny custom comment
    "App-Comment": `${localStorage.getItem(LOCAL_CLIENT_APP_COMMENT_KEY)}`,
    "X-Hasura-Client-Websocket": `true (this should not show up on read query)`,
  };
}

export function getCreateHttpHeaders(headersArray: HeadersBaseList): HttpHeaders {
  return new HttpHeaders(headersArray);
}
