import { InMemoryCache, NormalizedCacheObject } from "apollo-cache-inmemory";
import { WebSocketLink } from "apollo-link-ws";

import { HttpLink, HttpLinkHandler } from "apollo-angular-link-http";
import ApolloClient from "apollo-client";
import { split, ApolloLink } from "apollo-link";
import { getCreateApolloHttpLinkHandler, getCreateApolloWebSocketLink } from "./apollo-protocol-link-handler";
import { getIsWebSocketSubscriptionApolloOperation } from "./apollo-setup-utilities";

// tslint:disable-next-line: ban-types
export const apollofactory: Function = (httpLink: HttpLink) => {
  const [apolloHttpLinkHandler, apolloWebSocketLinkClient]: [
    HttpLinkHandler,
    WebSocketLink
  ] = getHttpAndWebSocketLinkClientHandlers(httpLink);

  const apolloSplitLink: ApolloLink = split(
    getIsWebSocketSubscriptionApolloOperation,
    apolloWebSocketLinkClient, // Split left if true; Real-time subscription through WebSocket is required.
    apolloHttpLinkHandler // Default right if http query mutation fragment everything else.
  );
  const apolloClient: ApolloClient<NormalizedCacheObject> = getCreateNewApolloClient(apolloSplitLink);

  return apolloClient;
};
/**
 * Sets up the global Apollo client for use with Hasura
 * @param httpLink Factory function provided in app module entry point
 */
export function factoryGetCreateApolloClientSplitSetup(httpLink: HttpLink): ApolloClient<NormalizedCacheObject> {
  const [apolloHttpLinkHandler, apolloWebSocketLinkClient]: [
    HttpLinkHandler,
    WebSocketLink
  ] = getHttpAndWebSocketLinkClientHandlers(httpLink);

  const apolloSplitLink: ApolloLink = split(
    getIsWebSocketSubscriptionApolloOperation,
    apolloWebSocketLinkClient, // Split left if true; Real-time subscription through WebSocket is required.
    apolloHttpLinkHandler // Default right if http query mutation fragment everything else.
  );
  const apolloClient: ApolloClient<NormalizedCacheObject> = getCreateNewApolloClient(apolloSplitLink);

  return apolloClient;
}

export function getCreateNewApolloClient(apolloSplitLink: ApolloLink): ApolloClient<NormalizedCacheObject> {
  const newApolloClient: ApolloClient<NormalizedCacheObject> = new ApolloClient({
    connectToDevTools: false,
    cache: new InMemoryCache(),
    // Determines when to use the proper WebSocket for real time subscription
    link: apolloSplitLink,
    queryDeduplication: true,
    defaultOptions: {
      watchQuery: {
        fetchPolicy: "cache-and-network",
      },
      query: {
        fetchPolicy: "network-only",
      },
      mutate: {},
    },
  });

  return newApolloClient;
}

export function getHttpAndWebSocketLinkClientHandlers(httpLink: HttpLink): [HttpLinkHandler, WebSocketLink] {
  const apolloHttpLinkHandler: HttpLinkHandler = getCreateApolloHttpLinkHandler(httpLink);
  const apolloWebSocketLinkClient: WebSocketLink = getCreateApolloWebSocketLink();

  return [apolloHttpLinkHandler, apolloWebSocketLinkClient];
}
