export const LOCAL_CLIENT_APP_COMMENT_KEY: string = "App-Comment";

export function getLocaleMidnightDateTime(): Date {
  // Default using machine or device's locale. Time zoned.
  const localeToday: Date = new Date();
  // Locale. Not UTC. See .setUTCHours for more information.
  const localeTodayMidnightEpoch: number = localeToday.setHours(0, 0, 0, 0);
  // Could use either the now-mutated localeToday 0000 or use the epoch return value and construct a new Date object. Equivalent.
  const localeTodayMidnightReset: Date = new Date(localeTodayMidnightEpoch);

  return localeTodayMidnightReset;
}
