import { HttpHeaders } from "@angular/common/http";
import { WebSocketLink } from "apollo-link-ws";

import { HttpLink, HttpLinkHandler, Options } from "apollo-angular-link-http";
import {
  GRAPHQL_ENDPOINT_HTTP_ADDRESS,
} from "../globals";
import { getCreateHttpHeaders, getDefaultApolloHeadersBase } from "./apollo-default-app-headers";
import { getHttpOptions, getWebSocketConfig } from "./apollo-options";
import { HeadersBaseList } from "./models/headers-base-list";

export function getCreateApolloHttpLinkHandler(
  httpLink: HttpLink,
  uri: string = GRAPHQL_ENDPOINT_HTTP_ADDRESS,
  headersArray: HeadersBaseList = getDefaultApolloHeadersBase()
): HttpLinkHandler {
  const headers: HttpHeaders = getCreateHttpHeaders(headersArray);

  const options: Options = getHttpOptions(uri, headers);

  return httpLink.create(options);
}
export function getCreateApolloWebSocketLink(
  apolloWebSocketConfig: WebSocketLink.Configuration = getWebSocketConfig()
): WebSocketLink {
  return new WebSocketLink(apolloWebSocketConfig);
}
