import { getMainDefinition } from "apollo-utilities";

import { Operation } from "apollo-link";
import { FragmentDefinitionNode, OperationDefinitionNode } from "graphql";

function getIsOperation(query: OperationDefinitionNode | FragmentDefinitionNode): query is OperationDefinitionNode {
  return (query as OperationDefinitionNode).operation !== undefined;
}
function getIsSubscriptionFromOperation({ operation }: OperationDefinitionNode): boolean {
  return operation === "subscription";
}
function getIsSubscriptionFromQueryDefinition(query: OperationDefinitionNode | FragmentDefinitionNode): boolean {
  return getIsOperation(query) && getIsSubscriptionFromOperation(query);
}
/**
 * Query handler to determine whether to split.
 *
 * @param query Apollo operation for handling
 */
export function getIsWebSocketSubscriptionApolloOperation({ query: gqlQueryDocument }: Operation): boolean {
  const operationOrFragmentDefinition: OperationDefinitionNode | FragmentDefinitionNode = getMainDefinition(
    gqlQueryDocument
  );

  return getIsSubscriptionFromQueryDefinition(operationOrFragmentDefinition);
}
