import { Provider } from "@angular/core";
import {  APOLLO_OPTIONS } from "apollo-angular";
import { HttpLink } from "apollo-angular-link-http";
import { factoryGetCreateApolloClientSplitSetup } from "./apollo-create";

export const apolloAppModuleProvider: Provider = {
  provide: APOLLO_OPTIONS,
  useFactory: factoryGetCreateApolloClientSplitSetup,
  /* HttpLink created and passed in to the factory function to call httpLink.create() */
  deps: [HttpLink],
};
