import { HttpHeaders } from "@angular/common/http";
import { WebSocketLink } from "apollo-link-ws";
import { ClientOptions } from "subscriptions-transport-ws";
import { GRAPHQL_ENDPOINT_WEBSOCKET_ADDRESS } from "../globals";
import { getDefaultApolloHeadersBase } from "./apollo-default-app-headers";

import { Options } from "apollo-angular-link-http";
import { HeadersBaseList } from "./models/headers-base-list";

function getApolloWebSocketOptions(headers: HeadersBaseList = getDefaultApolloHeadersBase()): ClientOptions {
  return {
    connectionParams: {
      headers,
      authToken: "test",
    },
    reconnect: true,
    connectionCallback: () => {},
  };
}

export function getHttpOptions(uri: string, headers: HttpHeaders): Options {
  return {
    uri,
    headers,
  };
}

export function getWebSocketConfig(
  uri: string = GRAPHQL_ENDPOINT_WEBSOCKET_ADDRESS,
  options: ClientOptions = getApolloWebSocketOptions()
): WebSocketLink.Configuration {
  return {
    uri,
    options,
  };
}
