import { HostListener, Injectable } from "@angular/core";
import { SwUpdate, UpdateActivatedEvent, UpdateAvailableEvent } from "@angular/service-worker";
const CONSOLE_LOG_CHROME_DEV_TOOLS_COLOURISER: string =
  "background: #444; color: #bada55; padding: 2px; border-radius:24px";
const CONSOLE_LOG_STYLE_REPLACE: string = "%c";
const UPDATE_COLOUR: string = CONSOLE_LOG_CHROME_DEV_TOOLS_COLOURISER;
// Import * as BeforeInstallPromptEvent from "mdn-before-install-prompt-event";

@Injectable({
  providedIn: "root",
})
export class PwaService {
  /**
   * Chrome and Firefox only
   * - FIXME: Provide workaround for iOS safari using native platform API
   */
  deferredInstallPromptEvent?: BeforeInstallPromptEvent;

  get isPwaInstallAvailable(): boolean {
    return this.deferredInstallPromptEvent !== undefined;
  }
  /**
   * Listen to this as soon as possible.
   * @param installPwaPromptEvent Fires once, quickly
   */
  // tslint:disable-next-line: no-unsafe-any
  @HostListener("window:beforeinstallprompt", ["$event"])
  // tslint:disable-next-line: deprecation
  onBeforeInstallPrompt(installPwaPromptEvent: BeforeInstallPromptEvent): void {
    console.log("PwaService -> onBeforeInstallPrompt -> installPwaPromptEvent", installPwaPromptEvent);
    console.log("%c", UPDATE_COLOUR, "PwaService -> onBeforeInstallPrompt -> event", installPwaPromptEvent);

    this.deferredInstallPromptEvent = installPwaPromptEvent;
  }

  constructor(public swUpdate: SwUpdate) {
    // tslint:disable-next-line: deprecation
    window.addEventListener("beforeinstallprompt", (event: BeforeInstallPromptEvent) => {
      console.log("PwaService -> constructor -> event", event);
      this.deferredInstallPromptEvent = event;
    });

    window.addEventListener("appinstalled", (appInstalledEvent: Event) => {
      console.log("PwaService -> constructor -> appInstalledEvent", appInstalledEvent);
    });

    swUpdate.activated.subscribe((updateInstalled: UpdateActivatedEvent) => {
      console.log("%c", UPDATE_COLOUR, "PwaService -> constructor -> updateInstalled", updateInstalled);
    });
    swUpdate.available.subscribe((update: UpdateAvailableEvent) => {
      console.log("%c", UPDATE_COLOUR, "PwaService -> constructor -> update", update);
    });
  }

  async promptUserChoiceInstallPwa(
    event: Event,
    // tslint:disable-next-line: deprecation
    promptToTrigger: BeforeInstallPromptEvent = this.deferredInstallPromptEvent
  ): Promise<{
    outcome: "accepted" | "dismissed";
    platform: string;
  }> {
    console.log("PwaService -> event", event, promptToTrigger);

    const userInstalledOrDismissed: Promise<{
      outcome: "accepted" | "dismissed";
      platform: string;
    }> = promptToTrigger.userChoice;

    return userInstalledOrDismissed;
  }
}
